import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Main from "../components/main"
import CardScroller from "../components/card-scroller"
import OptionCard from "../components/option-card"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <Main pageTitle="What can we help you with today?">
      <CardScroller>
        <OptionCard
          link="#"
          alt="alt text"
          cardTitle="Need a Technician"
        />
        <OptionCard
          link="#"
          alt="alt text"
          cardTitle="Repairs"
        />
        <OptionCard
          link="#"
          alt="alt text"
          cardTitle="New Door"
        />
        <OptionCard
          link="#"
          alt="alt text"
          cardTitle="New Opener"
        />
        <OptionCard
          link="#"
          alt="alt text"
          cardTitle="Commercial"
        />
        <OptionCard
          link="#"
          alt="alt text"
          cardTitle="Parts"
        />
        <OptionCard
          link="#"
          alt="alt text"
          cardTitle="DIY"
        />
        <OptionCard
          link="#"
          alt="alt text"
          cardTitle="About Us"
        />
      </CardScroller>
    </Main>
  </Layout>
)

export default IndexPage
